<template>
  <!---->
  <div class="container" style="padding:50px 0;">
    <ol class="breadcrumb">
      <li>
        <router-link :to="{name: 'service'}"> </router-link>
      </li>
      <li>
        <router-link :to="{name: 'selfHelp'}"> </router-link>
      </li>
      <li class="active"></li>
    </ol>

    <div class="content bg-white">
      <div class="list">
        <p class="title"></p>
        <p class="sub-title"></p>
        <ul>
          <li class="indent">
            
          </li>
          <li class="indent">
            
          </li>
          <li class="indent">
            15
          </li>
          <li class="indent">
            
          </li>
          <li class="indent">
            15~45/
          </li>
          <li class="indent">
            <br/>TF<br/><br/>
          </li>

          <div class="diliver-box">
            
          </div>

          <li>
            A
          </li>
          <li>
            
          </li>
          <li>
            0769-829294904004000-585-959
          </li>
          <li>
            8:30-18:00
          </li>
          <li>
            
          </li>
        </ul>
      </div>

      <div class="check-box">
        <img class="check-box-rect" src="../../assets/img/service/border_red.png" @click="onCheck()">
        <img class="check-box-hook" src="../../assets/img/service/gou.png" v-show="isCheck">
        <span v-if="isCheck">{{this.spanTxt}}</span>
        <span v-else>{{this.spanTxt}}</span>
      </div>

      <div class="title-info">
        
      </div>

      <div class="row" style="padding-left:300px;">
        <div class="service-icon" v-bind:class="{'bg-blue': isCheck, 'btn-limited': !isCheck}">
          <a @click.prevent="onJump('sendTerm')">
            <p style="font-size:22px;"></p>
            <p style="font-size:14px;"></p>
          </a>
        </div>

        <div class="service-icon" v-bind:class="{'bg-green': isCheck, 'btn-limited': !isCheck}">
          <a @click.prevent="onJump('checkTerm')">
            <p style="font-size:22px;"></p>
            <p style="font-size:14px;">()</p>
          </a>
        </div>

      </div>

    </div>
  </div>
</template>

<script>
export default {
  data() {
    return {
      isCheck: false,
      spanTxt: ''
    }
  },
  methods: {
    onCheck() {
      this.isCheck = !this.isCheck;
    },
    onJump(name) {
      if (this.isCheck)
        this.$router.push({ name: name });
      return;
    }
  }
}
</script>

<style scoped>
.content {
  width: 100%;
}
.title {
  font-size: 48px;
  color: #e60012;
  margin-bottom: 20px;
}
.sub-title {
  font-size: 22px;
  color: #333333;
  margin-bottom: 60px;
}
ul {
  list-style-type: none;
  padding-left: 0;
}
ul > li {
  position: relative;
  line-height: 34px;
  font-size: 16px;
  color: #575757;
}
.indent {
  text-indent: -35px;
  margin-left: 35px;
}
.list {
  padding: 70px 150px 50px;
}
.diliver-box {
  font-size: 22px;
  color: #333333;
  margin-top: 40px;
  margin-bottom: 20px;
  padding-bottom: 4px;
  border-bottom: 1px solid #dbdbdb;
}
.check-box {
  position: relative;
  text-align: center;
  height: 30px;
  line-height: 30px;
  font-size: 18px;
  color: #333333;
  padding-bottom: 60px;
}
.check-box-rect {
  position: absolute;
  cursor: pointer;
  top: 0;
  left: 430px;
}
.check-box-hook {
  position: absolute;
  pointer-events: none;
  z-index: 999;
  top: -10px;
  left: 430px;
}
.title-info {
  text-align: center;
  font-size: 22px;
  color: #404040;
}
.service-icon {
  float: left;
  width: 250px;
  height: 64px;
  border-radius: 6px;
  margin: 22px 27px;
}
.service-icon > a {
  position: relative;
  top: 50%;
  transform: translateY(-50%);
  width: 100%;
  display: inline-block;
  text-align: center;
  cursor: pointer;
}
.btn-limited {
  background: #c1c1c1;
  color: #575757;
}
.btn-limited > a {
  cursor: not-allowed;
}
.bg-blue {
  background: #5ec3f7;
  color: #fff;
}
.bg-green {
  background: #4cda64;
  color: #fff;
}
p {
  margin: 0;
}
</style>


