<template>
  <!---->
  <div class="container" style="padding:50px 0;">
    <ol class="breadcrumb">
      <li>
        <router-link :to="{name: 'service'}"> </router-link>
      </li>
      <li class="active"></li>
    </ol>

    <div class="content bg-white">
      <div class="row">
        <div class="type-icon">
          <type-radio class="radio" v-model="typeVal" value="1" txt="" :imgList=imgSrc_1 :checked="ischecked"></type-radio>
        </div>

        <div class="type-icon">
          <type-radio class="radio" v-model="typeVal" value="2" txt="" :imgList=imgSrc_2></type-radio>
        </div>

      </div>

      <div class="row">
        <div class="que-left">
          <p></p>
        </div>

        <div class="que-right">
          <type-btn v-model="btnVal" :btnList=btnList></type-btn>
        </div>

      </div>

      <div class="row dividing-line"></div>

      <div class="row">
        <div class="detail-info">
          <ul>
            <li v-for="(item, index) in showList" :key="index" :class="item.isRemark? 'imRemark':'imNotRemark'">
              {{item.msg}}
            </li>
          </ul>
        </div>
      </div>

      <div class="row">
        <p style="text-align:center;color:#404040;">
          <router-link :to="{name: 'saleTerm'}" style="color:#e60012;cursor:pointer;text-decoration:underline;"></router-link>
        </p>
      </div>

    </div>

  </div>
</template>

<script>

import TypeRadio from './ui/typeRadio.vue'
import pic_1_default from '@/assets/img/service/1_1.png'
import pic_2_default from '@/assets/img/service/1_2.png'
import pic_3_default from '@/assets/img/service/2_1.png'
import pic_4_default from '@/assets/img/service/2_2.png'

import TypeBtn from './ui/typeBtn.vue'

export default {
  components: { TypeRadio, TypeBtn },

  data() {
    return {
      typeVal: '',// 
      preType: '',// 
      ischecked: true,
      imgSrc_1: [
        { uri: pic_1_default },
        { uri: pic_3_default }
      ],
      imgSrc_2: [
        { uri: pic_2_default },
        { uri: pic_4_default }
      ],
      btnVal: '0',// 
      btnList: [],
      btnList_1: [
        { txt: '' },
        { txt: '' },
        { txt: '' },
        { txt: 'APP' }
      ],
      btnList_2: [
        { txt: '' },
        { txt: 'LCD' },
        { txt: '' },
        { txt: '' }
      ],
      showList: [],
      detailList: [
        [
          [{
            msg: '1?LCD3~5,,', isRemark: false
          }, {
            msg: '2,()',
            isRemark: false
          }, {
            msg: '3USB',
            isRemark: false
          }, {
            msg: '4USB,',
            isRemark: false
          }, {
            msg: '30',
            isRemark: true
          }],
          [{
            msg: '1',
            isRemark: false
          }, {
            msg: '2TB',
            isRemark: false
          }, {
            msg: '3USBU(K1M1USB)',
            isRemark: false
          }, {
            msg: '42014',
            isRemark: false
          }, {
            msg: 'TFTFTFTFTF',
            isRemark: true
          }],
          [{
            msg: '1USBPC,',
            isRemark: false
          }, {
            msg: '2MP3',
            isRemark: false
          }, {
            msg: '3TF',
            isRemark: false
          }, {
            msg: 'G63',
            isRemark: true
          }],
          [{
            msg: '1',
            isRemark: false
          }, {
            msg: '2',
            isRemark: false
          }, {
            msg: '3APP',
            isRemark: false
          }]
        ],
        [
          [{
            msg: '1?LCD3~5,,',
            isRemark: false
          }, {
            msg: '2,()',
            isRemark: false
          }, {
            msg: '3USB',
            isRemark: false
          }, {
            msg: '4USB,',
            isRemark: false
          }],
          [{
            msg: '1',
            isRemark: false
          }, {
            msg: '2LCD',
            isRemark: false
          }, {
            msg: '3',
            isRemark: false
          }, {
            msg: '4LCD',
            isRemark: false
          }],
          [{
            msg: '1',
            isRemark: false
          }, {
            msg: '2',
            isRemark: false
          }],
          [{
            msg: '1USB,',
            isRemark: false
          }, {
            msg: '2',
            isRemark: false
          }, {
            msg: '3U',
            isRemark: false
          }, {
            msg: '',
            isRemark: true
          }]
        ]
      ]
    }
  },

  created() {
    this.showList = this.detailList[0][0];
    this.preType = this.typeVal;
  },

  updated() {
    this.btnList = this.typeVal === "1" ? this.btnList_1 : this.btnList_2;


    let nowType = this.typeVal;
    if (nowType !== this.preType) {
      this.btnVal = 0;
      this.preType = nowType;
    }


    let i = parseInt(this.typeVal) - 1;
    let j = parseInt(this.btnVal);
    this.showList = this.detailList[i][j];
  }
}
</script>

<style scoped>
.content {
  width: 100%;
  height: 1238px;
}
.type-icon {
  float: left;
  margin: 108px 90px;
}
.type-icon:first-child {
  margin-left: 360px;
}
.type-icon:last-child {
  margin-right: 360px;
}
.que-left {
  float: left;
  margin-left: 106px;
  margin-right: 64px;
}
.que-left > p {
  margin: 0;
  font-size: 24px;
  color: #404040;
  display: inline-block;
}
.que-right {
  float: left;
}
.dividing-line {
  width: 988px;
  height: 1px;
  margin: 34px auto;
  border-top: 1px solid #dbdbdb;
}
.detail-info {
  margin-left: 230px;
  margin-right: 108px;
}
.imRemark {
  color: #929292;
}
.imNotRemark {
  color: #404040;
}
ul {
  list-style-type: none;
}
ul > li {
  position: relative;
  margin-bottom: 28px;
  line-height: 28px;
  clear: both;
  font-size: 16px;
  color: #404040;
}
</style>





