<template>
  <!---->
  <div class="container" style="padding:50px 0;">
    <ol class="breadcrumb">
      <li>
        <router-link :to="{name: 'service'}"> </router-link>
      </li>
      <li>
        <router-link :to="{name: 'selfHelp'}"> </router-link>
      </li>
      <li>
        <router-link :to="{name: 'saleTerm'}"> </router-link>
      </li>
      <li class="active"></li>
    </ol>

    <div class="content bg-white">

      <div class="row title-info">
        :
      </div>

      <div class="row">

        <div class="map-pos">
          <img src="../../assets/img/service/map.png" style="position:absolute;top:0;left:0;display:block;" alt="">
          <div class="map">
            <p id="" style="top: 35%; left: 16%;" @click="onTest"></p>
            <p id="" style="top: 58%; left: 18%;" @click="onTest"></p>
            <p id="" style="top: 48%;left: 37%;" @click="onTest"></p>
            <p id="" style="top: 37%;left: 55%;" @click="onTest"></p>
            <p id="" style="top: 48%;left: 52%;" @click="onTest"></p>
            <p id="" style="top: 42%;left: 57%;width: 20px;" @click="onTest"></p>
            <p id="" style="top: 63%;left: 50%;" @click="onTest"></p>
            <p id="" style="top: 81%;left: 46%;" @click="onTest"></p>
            <p id="" style="top: 51%;left: 62%;width: 20px;" @click="onTest"></p>
            <p id="" style="top: 65%;left: 60%;" @click="onTest"></p>
            <p id="" style="top: 74%;left: 59%;" @click="onTest"></p>
            <p id="" style="top: 83%;left: 61%;" @click="onTest"></p>
            <p id="" style="top: 41%;left: 68%;width: 20px;" @click="onTest"></p>
            <p id="" style="top: 42%;left: 73%;" @click="onTest"></p>
            <p id="" style="top: 53%;left: 70%;" @click="onTest"></p>
            <p id="" style="top: 61%;left: 68%;" @click="onTest"></p>
            <p id="" style="top: 69%;left: 69%;width: 20px;" @click="onTest"></p>
            <p id="" style="top: 82%;left: 73%;" @click="onTest"></p>
            <p id="" style="top: 95%;left: 65%;" @click="onTest"></p>
            <p id="" style="top: 13%;left: 90%;" @click="onTest"></p>
            <p id="" style="top: 23%;left: 91%;" @click="onTest"></p>
            <p id="" style="top: 30%;left: 85%;" @click="onTest"></p>
            <p id="" style="top: 35%;left: 75%;" @click="onTest"></p>
            <p id="" style="top: 39%;left: 78%;" @click="onTest"></p>
            <p id="" style="top: 46%;left: 79%;" @click="onTest"></p>
            <p id="" style="top: 56%;left: 79%;width: 20px;" @click="onTest"></p>
            <p id="" style="top: 69%;left: 77%;width: 20px;" @click="onTest"></p>
            <p id="" style="top: 52%;left: 84%;width: 20px;" @click="onTest"></p>
            <p id="" style="top: 59%;left: 87%;" @click="onTest"></p>
            <p id="" style="top: 64%;left: 86%;width: 20px;" @click="onTest"></p>
            <p id="" style="top: 72%;left: 83%;width: 20px;" @click="onTest"></p>
            <p id="" style="top: 79%;left: 89%;width: 20px;" @click="onTest"></p>
            <p id="" style="top: 85%;left: 76%;" @click="onTest"></p>
            <p id="" style="top: 87%;left: 72%;" @click="onTest"></p>
          </div>
        </div>

      </div>

      <div class="list">
        <div class="row">
          <div class="sub-title">
            <p>{{curId}}</p>
          </div>
        </div>

        <div class="row">
          <div class="table-responsive">
            <table class="table">
              <tbody v-for="(items, index) in curList" :key="index">
                <tr>
                  <td>
                    {{items[0]}}
                  </td>
                  <td>
                    {{items[3]}}
                  </td>
                  <td>
                    {{items[1]}}
                  </td>
                  <td>
                    {{items[2]}}
                  </td>
                </tr>
              </tbody>
            </table>

          </div>
        </div>
      </div>

    </div>
  </div>
</template>

<script>

import mapData from "./data/map.js";

export default {
  name: 'checkTerm',

  data() {
    return {
      mapData: mapData,

      curId: '',
      curList: [
        ["", "", "020-38059399", "3101"],
        ["", "", "0752-2047666", "2430"],
        ["", "", "13076918303", "8"]
      ]
    }
  },


  methods: {
    onTest(event) {
      let _id = event.target.id;

      if (_id === this.curId) {
        return;
      } else {

        let _hasFound = false;
        this.curId = _id;

        this.clearList();
        for (let i = 0; i < this.mapData.length; ++i) {
          if (this.mapData[i].p === _id) {
            _hasFound = true;

            for (let j = 0; j < this.mapData[i].city.length; ++j) {
              this.curList.push(this.mapData[i].city[j]);
            }
            break;
          }
        }

        if (!_hasFound) {
          this.curList = [
            ["", "", "", ""]
          ]
        }
      }
    },

    clearList() {
      if (this.curList.length > 0) {
        this.curList = [];
      }
    },
  }

}
</script>

<style scoped>
.content {
  width: 100%;
  min-height: 1200px;
  height: auto !important;
  height: 1200px;
}
.title-info {
  padding-top: 50px;
  padding-bottom: 50px;
  font-size: 24px;
  color: #333333;
  font-weight: bold;
  text-align: center;
}
.map-pos {
  position: relative;
  float: left;
  top: 0;
  left: 240px;
  width: 699px;
  height: 576px;
}
.map {
  position: absolute;
  top: 0;
  left: -10px;
  width: 100%;
  height: 100%;
}
.map p {
  position: absolute;
  margin: 0;
  padding: 0;
  cursor: pointer;
  font-size: 14px;
}
.map p:hover {
  color: #fff;
}
.sub-title {
  float: left;
  width: 100px;
  text-align: center;
}
.sub-title > p {
  font-size: 24px;
  font-weight: bold;
}
.list {
  padding: 0 100px 50px;
}
.table-responsive {
  min-height: 0.01%;
  overflow-x: auto;
}
.table {
  width: 100%;
  max-width: 100%;
  margin-bottom: 20px;
  background-color: transparent;
  border-spacing: 0;
  border-collapse: collapse;
}
.table > tbody > tr > td,
.table > tbody > tr > th,
.table > tfoot > tr > td,
.table > tfoot > tr > th,
.table > thead > tr > td,
.table > thead > tr > th {
  padding: 8px;
  line-height: 1.42857143;
  vertical-align: top;
  border-top: 1px solid #ddd;
}
</style>



