import NotFound from '@/components/error/notFound.vue'
import MainContainer from '@/components/index.vue'


/*****************  ****************************/
import Home from '@/components/home/index.vue'

/*****************  ****************************/
import Products from '@/components/products/index.vue'
import ProductCompared from '@/components/products/comparedPage.vue'
import ProductDownload from '@/components/products/downloadPage.vue'
import ProductReport from '@/components/products/reportPage.vue'
import ProductDetail from '@/components/products/detailPage.vue'


/*****************  ****************************/
import Content from '@/components/content/index.vue'
import ContentDetail from '@/components/content/contentDetail.vue'
import VideoPage from '@/components/content/videoPage.vue'

/*****************  ****************************/
import Brand from '@/components/brand/index.vue'

/*****************  ****************************/
import Service from '@/components/service/index.vue'
import SelfHelp from '@/components/service/selfHelp.vue'
import MaintainCheck from '@/components/service/maintainCheck.vue'
import DealerCheck from '@/components/service/dealerCheck.vue'
import FakeCheck from '@/components/service/fakeCheck.vue'
import NetworkCheck from '@/components/service/networkCheck.vue'
import SaleTerm from '@/components/service/saleTerm.vue'
import SendTerm from '@/components/service/SendTerm.vue'
import CheckTerm from '@/components/service/CheckTerm.vue'

/*****************  ****************************/
import ContactPage from '@/components/others/contactPage.vue'         // 
import CopyrightPage from '@/components/others/copyrightPage.vue'     // 
import FeedbackPage from '@/components/others/feedbackPage.vue'       // 
import BrandinfoPage from '@/components/others/brandinfoPage.vue'     // 
import SearchPage from '@/components/common/searchPage.vue'           // 

export default [
  {
    path: '/',
    redirect: '/pages'
  },
  {
    path: '/pages',
    redirect: '/pages/home',
    component: MainContainer,
    children: [
      // ------------------------------------------
      {
        name: 'home',
        path: '/pages/home', // 
        component: Home
      },

      // ------------------------------------------
      {
        name: 'products',
        path: '/pages/products', // 
        component: Products
      },
      {
        name: 'productCompared',
        path: '/pages/products/compared', // 
        component: ProductCompared
      },
      {
        name: 'productDetail',
        path: '/pages/products/detail', // 
        component: ProductDetail
      },
      {
        name: 'productReport',
        path: '/pages/products/report', // 
        component: ProductReport
      },
      {
        name: 'productDownload',
        path: '/pages/products/download', // 
        component: ProductDownload
      },

      // ------------------------------------------
      {
        name: 'content',
        path: '/pages/content', // 
        component: Content
      },
      {
        name: 'contentDetail',
        path: '/pages/content/contentDetail', // 
        component: ContentDetail
      },
      {
        name: 'videoPage',
        path: '/pages/content/videoPage', // 
        component: VideoPage
      },

      // ------------------------------------------
      {
        name: 'brand',
        path: '/pages/brand', // 
        component: Brand
      },

      // ------------------------------------------
      {
        name: 'service',
        path: '/pages/service', // 
        component: Service,
      },
      {
        name: 'selfHelp',
        path: '/pages/service/selfHelp', // 
        component: SelfHelp
      },
      {
        name: 'maintainCheck',
        path: '/pages/service/maintainCheck', // 
        component: MaintainCheck
      },
      {
        name: 'dealerCheck',
        path: '/pages/service/dealerCheck', // 
        component: DealerCheck
      },
      {
        name: 'fakeCheck',
        path: '/pages/service/fakeCheck', // 
        component: FakeCheck
      },
      {
        name: 'networkCheck',
        path: '/pages/service/networkCheck', // 
        component: NetworkCheck
      },
      {
        name: 'saleTerm',
        path: '/pages/service/selfHelp/saleTerm', // 
        component: SaleTerm
      },
      {
        name: 'sendTerm',
        path: '/pages/service/selfHelp/saleTerm/sendTerm', // 
        component: SendTerm
      },
      {
        name: 'checkTerm',
        path: '/pages/service/selfHelp/saleTerm/checkTerm', // 
        component: CheckTerm
      },

      // ------------------------------------------
      {
        name: 'contact',
        path: '/pages/others/contact', // 
        component: ContactPage
      },
      {
        name: 'copyright',
        path: '/pages/others/copyright', // 
        component: CopyrightPage
      },
      {
        name: 'feedback',
        path: '/pages/others/feedback', // 
        component: FeedbackPage
      },
      {
        name: 'brandinfo',
        path: '/pages/others/brandinfo', // 
        component: BrandinfoPage
      },
      {
        name: 'searchpage',
        path: '/pages/others/search', // 
        component: SearchPage
      }
    ]
  },
  { // 404 NotFound 
    path: '*',
    component: NotFound
  }
]



